@charset "UTF-8";
@import "_variables", "_mixins", "_functions";

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, a, abbr, address, em, img, strong, dl, dt, dd, ol, ul, li, fieldset, form, textarea, button, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0 none; font-size: 100%; font-weight:normal; vertical-align: top; }

/* HTML5 display-role reset for older browsers */
section, article, aside, details, figcaption, figure, footer, header, hgroup, main, menu, nav { display: block; }

a {color:inherit; text-decoration: none;}

ol, ul, li { list-style: none; }

button {display: inline-block; cursor: pointer; background:none;}

label {display: inline-block; cursor:pointer;}

input, label, select, textarea {margin:0; vertical-align: middle; }

select { font-size: 12px; font-family: Dotum, "돋움", sans-serif;}

em {font-style:normal;}

table { table-layout: fixed; width: 100%; }

table, th, td {border-collapse:collapse; border-spacing: 0; border: 0; vertical-align: middle; word-break:break-word;}

table thead th { text-align: center; }

table caption { overflow: hidden; position: absolute; top: -10000px; left: -10000px; width: 0; height: 0; font-size: 0; line-height: 0; visibility: hidden; }


/* display none for screen reader software */
legend, hr { overflow: hidden; position: absolute; left: 0; top: 0; width: 0; height: 0; font-size: 0; line-height: 0; visibility: hidden; }

/* skipNav */
#skipnav { position: absolute; top: 0; left:0 ; z-index:1020; width:100%;}

#skipnav a { position: absolute; top: -9999px; }

#skipnav a:hover, #skipnav a:active, #skipnav a:focus {display:block; top: 0; left:0; width: 100%; padding: 5px 0; color:yellow; font-weight: bold; text-align: center; background-color: #000;}






